/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/

body,
button,
input,
select,
textarea {
	color: $color__p;
	font-family: $font__main;
	font-size: 1rem;
	line-height: $font__line-height-body;
}

/* Headings */

h1, h2, h3, h4, h5, h6 {
	clear: both;
}
h1 {
	color: $color__h1;
}
h2 {
	color: $color__h2;
	font-size: 1.25rem;
	font-weight: 300;
	@include tablet {
		font-weight: 400;
	}
	a {
		color: $color__link;
		font-weight: bold;
		font-size: 1.5rem;
	}
}

/* Copy */

p {
	margin-bottom: $base__space-unit;
	color: $color__p;
	font-size: 1.25rem;
	font-weight: 300;
	@include tablet {
		font-size: 1.75rem;
	}
	@include desktop {
		font-size: 2.5rem;
	}
}

.fas {
	color: $color__link;
	font-size: 125px;
	margin-top: 1.5rem;
}

dfn, cite, em, i {
	font-style: italic;
}

blockquote {
	margin: 0 $base__space-unit;
}

address {
	margin: 0 0 $base__space-unit;
}

pre {
	background: $color__background-pre;
	font-family: $font__pre;
	font-size: .9375rem;
	line-height: $font__line-height-pre;
	margin-bottom: 1.6rem;
	max-width: 100%;
	overflow: auto;
	padding: 1.6rem;
}

code, kbd, tt, var {
	font-family: $font__code;
	font-size: .9375rem;
}

abbr, acronym {
	border-bottom: 1px dotted $color__border-abbr;
	cursor: help;
}

mark, ins {
	background: $color__background-ins;
	text-decoration: none;
}

big {
	font-size: 125%;
}
